 body{
   height:100vh;
   background: #0F2027;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #2C5364, #203A43, #0F2027); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
 }
 .header-color{
/*   background: #833ab4;  
  background: -webkit-linear-gradient(to right, #fcb045, #fd1d1d, #833ab4);  
  background: linear-gradient(to right, #fcb045, #fd1d1d, #833ab4); */
  background: #0f0c29;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #24243e, #302b63, #0f0c29); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

 }
.homepage-grid{
 /*  background: #833ab4;  
  background: -webkit-linear-gradient(to right, #fcb045, #fd1d1d, #833ab4);  
  background: linear-gradient(to right, #fcb045, #fd1d1d, #833ab4);  */
  background: #0F2027;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #2C5364, #203A43, #0F2027); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  text-align: center;
  width:100%;
  height: 100%;
   
}
.avatar-img{
  
  height:200px;
  
  border-radius: 100px;
  
}
.banner-text{
  background-color: black;
  opacity: .7;
  width:75%;
  margin:auto;
  border-radius: 10px;
  padding:0px;
}
.banner-text h1{
  font-size:60px;
  font-weight: bold;
  color:white;
  padding: 0;
}
.banner-text hr{
  border-top:5px dotted white;
  width:50%;
  margin:auto;
  padding:0;
}
.banner-text p{
  color:white;
  padding:1em;
  font-size: 20px;
}
.social-links{
  display: flex;
  justify-content: space-around;
  width:50%;
  margin:auto;
}
.social-links i {
  color:white;
  font-size:100px;
  padding:10px;
}
.Projects-container{
  display:flex;
  flex-wrap: wrap;
}
.projectCards{
 
  width:350px;
  margin: auto;
}
@media(max-width:700px){
  .projectCards{
    width:150px;
    margin-bottom: 10px;
  }
  .social-links i {
    color:white;
    font-size:50px;
    padding:5px;
  }
  .banner-text h1{
    font-size:30px;
  }
}
@media(min-width:300px){
  .projectCards{
    width:150px;
  }
  .social-links i {
    color:white;
    font-size:50px;
    padding:5px;
  }
}
.cardActions{
  width:100%;
  display:flex;
  align-items: center;
  box-sizing: border-box;
}
.cardActions a{
 padding-bottom: none;
 margin-right:10px ; 
}
.cardActions a i{
  font-size:40px;
}
.tabs{
  color:white;
}
.contact-body{
 margin:auto;
 position: fixed; 
 margin:auto;
 height: 100%;
 width:100%;
 justify-content: center;
 padding-top:2em;
}
@media(max-width:450px){
    .contact-body{
        
        position: static;
       }
       
       .contacts-list .list-items i{
        padding:0;
        font-size: 40px;
        margin:auto;
        
     }
}
.contact-grid{
    color:black;
    background: white;
    width:80%;
    justify-content: center;
    align-items: center;
    height: 550px;;
    text-align: center;
  
    
}
.contact-grid h2{
    font-family: 'Allan',sans-serif,cursive;
}
.contact-grid p{
    font-family: 'Oxygen', sans-serif,cursive;
    font-weight: bold;
}
.contact-message{
    margin:auto;
    padding-top:1em;
    width:75%;

}
.contact-grid hr{
    border-top:5px dotted black;
}
.contacts-list i{
   padding-right: 1em;
   font-size: 66px;
   margin:auto;
}
.list-items{
    font-size:25px;
    font-family:'Allan';
}

.profile-right-col{
    background:rgb(25, 43, 48);
    border:1px solid red;
    color:white;
    padding:2em;
    
}
.job-description{
    font-size: 25px;
    font-weight: lighter;
    font-family: 'Courier New', Courier, monospace;
    color:gray;
}
.bio-grid p{
   font-size:20px;
   font-weight: lighter;
 }
 .contact-section{
     text-align: right;
 }
 .contact-section p{
     font-size: 20px;
 }
 h1 ,h2{
     color:red;
 }
 h5{
     color:rgb(231, 90, 8);
 }
 .tertiary{
    margin-top:0px;
 }
 .tertiary h4{
     margin:0px;
 }
 .resume-avatar{
    height:200px;
    border-radius:100px;
 }
 .secondary p{
    font-size:15px;
 }
 .secondary h4{
    margin:0;
    padding:0;
 }
 @media(max-width:700px){
   .resume-avatar{
       height:150px;
   }
   .profile-left-col h2{
       font-size:20px;
       margin:2px;

   }
   .job-description{
    font-size: 10px;
    margin-top:2px;
    color:white;
    }
    .contact-section{
        text-align: left;
    }
    .contact-section p{
        font-size:12px;
        
    }
    .contact-section h4{
        font-size:15px;
        margin:0px;
        padding:0px;
    }
   
    .bio-grid p{
        font-size:10px;
        font-weight: lighter;
        margin :0;
        padding:0;
    }
    .education h1{
        font-size:30px;
        color:red;
        margin:0px;
    }
    .education h2{
        font-size:22px;
        color:red;
        margin:0px;
    }
    .tertiary h4{
        font-size:15px;
    }
    .tertiary p{
        font-size: 10px;
        padding: 0px;
        margin:0px;
    }
    .secondary p{
        font-size:10px;
        margin:0px;
        padding: 0px;
     }
     .secondary h4{
       font-size:15px;
       margin-top:0px;
       padding:0px;
     }
     .experience-container h1{
         font-size: 23px;
     }
 }

.company-description{
    font-family: 'Courier New', Courier, monospace;
    font-size: 2em;
    font-style: oblique;   
}
@media(max-width:700px){
    .company-description {
        font-size:1em;
    }
    .work-duties{
        display:none
    }
}

